  content: '';
  display: table;
}

.single-portfolio__content-wrap p {
  margin: 0;
}

.single-portfolio__title {
  text-transform: uppercase;
}

.single-portfolio__inner {
  max-width: 768px;
  margin: 0 auto;
  padding: 8rem 0;
}

.single-portfolio__inner p {
  margin-bottom: 3rem;
}

.single-portfolio__inner p:only-child {
  margin-bottom: 0;
}

.single-portfolio__inner p:last-child {
  margin-bottom: 0;
}

.single-portfolio__gallery::after {
  clear: both;
  content: '';
  display: table;
}

.single-portfolio__hr {
  width: 120px;
  margin: 30px auto;
  border: 2px solid #16D6D9;
}

.single-portfolio__sub-content p {
  font-size: 1.8rem;
  margin: 0 0 2rem 0;
}

@media (min-width: 1024px) {
  .single-portfolio__sub-content p {
    font-size: 2rem;
  }
}

@media (min-width: 1280px) {
  .single-portfolio__sub-content p {
    font-size: 2.4rem;
  }
}

.single-portfolio__image {
  display: block;
  width: 100%;
}

.single-portfolio__image-grid {
  lost-column: 1/2 2 0px;
}

@media (min-width: 540px) {
  .single-portfolio__image-grid {
    lost-column: 1/2 2 0px;
  }
}

.single-portfolio__column--left {
  lost-column: 4/12;
}

.single-portfolio__column--right {
  lost-column: 8/12;
}

.is-open .single-portfolio__content-wrap {
  opacity: 1;
  transition: all ease-in .7s;
}

body.noscript .single-portfolio__content-wrap,
body.single-portfolio .single-portfolio__content-wrap {
  opacity: 1;
}

.hero {
  height: 100vh;
  position: relative;
  padding: 4rem;
}

.hero__content {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  text-align: center;
  max-width: 1024px;
  margin: 0 auto;
}

.hero__title {
  position: relative;
  color: #fff;
  text-transform: uppercase;
  font-family: "geomanist-black-webfont", Helvetica, Arial, sans-serif;
  font-size: 2.5rem;
}

@media (min-width: 1024px) {
  .hero__title {
    font-size: 8rem;
  }
}

@media (min-width: 1280px) {
  .hero__title {
    font-size: 11rem;
  }
}

.hero__word {
  transform: translateY(65px);
  display: inline-block;
  opacity: 0;
}

.hero__word:last-of-type {
  display: block;
}

.animate {
  transform: translateY(0px);
  transition: all 300ms ease 1000ms;
  opacity: 1;
}

.hero--gradient {
  animation: gradient_animate 50s ease infinite;
  background-color: #000;
}

@keyframes gradient_animate {
  0% {
    background-position: 84% 0;
  }
  50% {
    background-position: 17% 100%;
  }
  100% {
    background-position: 84% 0;
  }
}

