.chevron__wrapper {
  display: block;
  position: absolute;
  width: 100%;
}

.down-chevron {
  display: inline-block;
  fill: #FFFFFF;
  font-size: 15rem;
  line-height: 1;
}

.down-chevron g:not(:last-child) {
  display: none;
}

.down-chevron.loaded {
  animation: bumpChevron 4.2s infinite;
}

.js .down-chevron g {
  display: inline;
}

.js .down-chevron g:not(:first-child) {
  display: none;
}

.down-chevron.animate {
  opacity: 1;
}

body.noscript .hero__word,
body.single-portfolio .hero__word {
  opacity: 1;
  transform: translateY(0px);
}

.about {
  background-color: #e1e1e1;
  max-height: 0;
  overflow: hidden;
  position: relative;
  width: 100%;
  z-index: -1;
  transition: all ease-in-out .5s;
  color: #fff;
  transform: scale(0.8) translateZ(-400px);
  transform-origin: 50% 50%;
}

.about .single-portfolio__inner {
  mix-blend-mode: difference;
  padding-left: 2rem;
  padding-right: 2rem;
}

.about__title {
  text-transform: uppercase;
}

.about h2 {
  margin-bottom: 0;
  font-size: 2rem;
}

@media (min-width: 1024px) {
  .about h2 {
    font-size: 3rem;
  }
}

@media (min-width: 1280px) {
  .about h2 {
    font-size: 3.5rem;
  }
}

@keyframes pulse {
  0% {
    opacity: 1;
    display: none;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.about a {
  color: currentColor;
}

.about.is-active {
  max-height: 1000px;
  transform: scale(1);
}

.prev-next-navigation {
  border-bottom: solid 1px #eee;
  border-top: solid 1px #eee;
  padding: 2rem 0;
  display: none;
}

.prev-next-navigation::after {
  clear: both;
  content: '';
  display: table;
}

@media (min-width: 1024px) {
  .prev-next-navigation {
    display: block;
  }
}

.prev-next-navigation__icon {
  font-size: 2rem;
  vertical-align: middle;
}

.prev-next-navigation__prev, .prev-next-navigation__next {
  font-size: 1.2rem;
  lost-column: 1/1;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin: 0;
}

@media (min-width: 1024px) {
  .prev-next-navigation__prev, .prev-next-navigation__next {
    font-size: 1.2rem;
  }
}

@media (min-width: 1280px) {
  .prev-next-navigation__prev, .prev-next-navigation__next {
    font-size: 1.2rem;
  }
}

@media (min-width: 540px) {
  .prev-next-navigation__prev, .prev-next-navigation__next {
    lost-column: 1/2;
  }
}

.prev-next-navigation__prev a, .prev-next-navigation__next a {
  color: #000;
  display: inline-block;
  position: relative;
  transition: transform .3s ease-in-out;
}

.prev-next-navigation__prev a span, .prev-next-navigation__next a span {
  display: inline-block;
  vertical-align: middle;
}

.prev-next-navigation__prev strong, .prev-next-navigation__next strong {
  font-family: "geomanist-black-webfont", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.8rem;
}

@media (min-width: 1024px) {
  .prev-next-navigation__prev strong, .prev-next-navigation__next strong {
    font-size: 1.8rem;
  }
}

@media (min-width: 1280px) {
  .prev-next-navigation__prev strong, .prev-next-navigation__next strong {
    font-size: 1.8rem;
  }
}

.prev-next-navigation__next {
  text-align: right;
}

.prev-next-navigation__next a {
  transform: translate3d(170px, 0, 0);
}

.prev-next-navigation__next a:hover {
  transform: translate3d(0, 0, 0);
}

.prev-next-navigation__prev {
  text-align: left;
}

.prev-next-navigation__prev a {
  transform: translate3d(-170px, 0, 0);
}

.prev-next-navigation__prev a:hover {
  transform: translate3d(0, 0, 0);
}

.prev-next-navigation__image {
  display: inline-block;
  vertical-align: middle;
  width: 160px;
}

.prev-next-navigation__image--prev {
  margin-right: 10px;
}

.prev-next-navigation__image--next {
  margin-left: 10px;
}
